<template>
  <v-navigation-drawer absolute permanent right app class="mb-right-sidebar float-left text-left">
    <v-text-field v-model="search" append-icon="mdi-magnify" label="Search" single-line hide-details class="mb-1 pa-4"></v-text-field>
    <v-treeview :items="items" :search="search" dense open-all hoverable>
      <template v-slot:label="{ item }">
        <router-link :to="item.link" class="text-style" :active-class="currentUrl == item.link ? 'active-item': ''"> {{item.name}} </router-link> 
      </template>
    </v-treeview>
  </v-navigation-drawer>
</template>

<script>
  export default {
    props: ['menu-items', 'server-route'],
    data: () => ({
      currentUrl: "",
      search:"",
    }),
    computed: {
      items () {
        return [
          { id: 1, name: 'All Networks', children: this.getTreeViewData(this.menuItems), type: this.serverRoute, link:`/${this.serverRoute}/networks` },
        ]
      },
    },
    watch: {
      '$route.path': {
        handler: function(path) {
          this.currentUrl = path;
        },
        deep: true,
        immediate: true
      },
    },
    mounted() { },
    methods: {
      getTreeViewData(networksData){
        if(!networksData || networksData && networksData.length == 0){
          return;
        }
        const result = networksData.map(nw=>{
          let nwName = nw.name  ? nw.name : nw.network_name; //=> modbus/rubix: name,  bacnet-master: network_name
          let nwUUID = nw.uuid  ? nw.uuid : nw.network_uuid;
          if(nw.devices && nw.devices.length > 0){
            const devices = [];
            nw.devices.forEach(de => {
              let deName = de.name  ? de.name : de.device_name; //=> modbus/rubix: name,  bacnet-master: device_name
              let deUUID = de.uuid  ? de.uuid : de.device_uuid;
              const device = { id: deUUID, name: `Device: ${deName}`, type: 'device', link: `/${this.serverRoute}/devices/${deUUID}/points` };
              devices.push(device);
            });
            return { id: nwUUID, name: `Network: ${nwName}`, children: devices, type: 'network', link: `/${this.serverRoute}/networks/${nwUUID}/devices` };
          }
          else{
            return { id: nwUUID, name: `Network: ${nwName}`, type: 'network', link: `/${this.serverRoute}/networks/${nwUUID}/devices` };
          }
        });
        return result;
      },

    },
  };
</script>
<style >
.mb-right-sidebar .text-style{
  text-decoration: unset;
  color: unset;
  font-size: 0.8125rem;
  line-height: 1rem;
}
.active-item {
  font-weight: bold !important;
  font-size: 1rem !important;
}
.mb-right-sidebar .v-treeview-node__level {
  width: 10px;
}
</style>

