<template>
  <div v-bind:style="{ height: this.containerHeight, overflowY: 'auto', overflowX: 'hidden' }">
    <slot name="children"></slot>
  </div>
</template>
<script>
export default {
  name: 'vertical-height-container',
  props: ['vh'],
  computed: {
    containerHeight() {
      return `${this.vh}vh`;
    },
  },
};
</script>
